
class Quiz extends Phaser.Sprite {

    constructor(game, x, y, key1, key2) {
      
        super(game, x, y, key1);
        this.game = game;
        
        this.center = {x: x, y: y};

        this.quizzer = "";
        this.result = -1;

        this.choices = [-1,-1,-1];
        this.correct = -1;
        
        this.textChoices = [];
        
        var sprite1 = this.game.add.sprite(x*0.26, y*0.16, key1);
        sprite1.scale.setTo(x*0.0017,y*0.0010);

        var sprite11 = this.game.add.sprite(x*0.46, y*0.70, key2);
        sprite11.scale.setTo(x*0.00055,y*0.0006);
        var sprite12 = this.game.add.sprite(x*0.82, y*0.70, key2);
        sprite12.scale.setTo(x*0.00055,y*0.0006);
        var sprite13 = this.game.add.sprite(x*1.18, y*0.70, key2);
        sprite13.scale.setTo(x*0.00055,y*0.0006);
    }
    
    writeMain() {
      this.calcQuizzer();
      console.log('writeMain - result='+this.result);
      this.main = this.game.add.text(this.center.x*0.78+window.config.moveMainBy, this.center.y*0.23, this.quizzer, 
      {fontSize: window.config.quizSize+'px'});
      this.main.addColor("#ffffff", 0);
    }
    
    writeChoices() {
      console.log('writeChoices');
      this.calcChoices();
      var choices = "";
      this.choices.forEach(function (value) {
        choices = choices + " " + value;
      });
      console.log(choices);
      console.log(this.correct);
      var text = null;
      text = this.game.add.text(this.center.x*0.57, this.center.y*0.79, this.choices[0], 
      {fontSize: window.config.choiceSize+'px'});
      this.textChoices.push(text);
      text = this.game.add.text(this.center.x*0.93, this.center.y*0.79, this.choices[1], 
      {fontSize: window.config.choiceSize+'px'});
      this.textChoices.push(text);
      text = this.game.add.text(this.center.x*1.29, this.center.y*0.79, this.choices[2], 
      {fontSize: window.config.choiceSize+'px'});
      this.textChoices.push(text);
    }
    
    calcChoices() {
      var context = this;
      for (var index in this.choices) {
        this.choices[index] = this.getRandomFromResult();
      }
      var which = Math.floor(Math.random() * (1 + 2)) + 0;
      this.choices[which] = this.result;
      this.correct = which;
    }
    
    calcQuizzer() {
      console.log("calcRandomQuizzer");
      var response = window.config.calculator();
      this.result = response[0];
      this.quizzer = response[1];
    }
    
    erase() {
      if (this.main != null) {
        this.main.destroy();
      }
      this.quizzer = "";
      this.result = -1;
      for (var index in this.textChoices) {
        this.textChoices[index].destroy();
      }
    }
    
    getResult() {
      return this.result;
    }
    
    getCorrect() {
      return this.correct;
    }
    
    getRandomFromResult() {
      return window.config.getRandom(this.result);
    }

}

export default Quiz;
