
class Status extends Phaser.Sprite {

    constructor(game, x, y, key) {
      
        super(game, x, y, key);
        
        this.center = {x: x, y: y};
        
        this.game = game;
        
        this.key = key;
        
        // this.mainStatus = this.game.add.text(0, 0, "");
        this.mainStatus = null;
        this.mainSprite = null;
        
        this.scores = [0, 0, 0];
        
        this.textScores = [];
        
        this.writeScores();

        this.game.add.text(this.center.x-315, this.center.y*1.86, "blue", 
        {fontSize: '34px', fill: "#0000a2"});
        this.game.add.text(this.center.x-35, this.center.y*1.86, "red", 
        {fontSize: '34px', fill: "#8b0000"});
        this.game.add.text(this.center.x+240, this.center.y*1.86, "green", 
        {fontSize: '34px', fill: "#006400"});
    }
    
    writeMainStatus(msg) {
      console.log('writeMainStatus: '+msg); 
      
      this.erase();
      
      this.mainStatus = this.game.add.text(this.center.x*0.30, this.center.y*1.38, msg, {
          font: "90px Optima",
          fill: "#FFD700"
      });
      this.mainStatus.setShadow(4, 4, 'rgba(0,0,0,0.9)', 6);
      
      this.mainSprite = this.game.add.sprite(0, this.center.y*1.32, this.key);
      this.mainSprite.scale.setTo(this.center.x*0.0032,this.center.y*0.0013);
    }
    
    updateScores(arrayPlus) {
      this.scores[0] += arrayPlus[0];
      this.scores[1] += arrayPlus[1];
      this.scores[2] += arrayPlus[2];
    }
    
    writeScores() {
      console.log('writeScores');
    
      for (var index in this.textScores) {
        this.textScores[index].destroy();
      }
      this.textScores = [];
      
      var text = null;
      text = this.game.add.text(this.center.x-300, this.center.y*1.72, this.scores[0], 
      {fontSize: '80px', fill: "#0000a2"});
      this.textScores.push(text);
      text = this.game.add.text(this.center.x-30, this.center.y*1.72, this.scores[1], 
      {fontSize: '80px', fill: "#8b0000"});
      this.textScores.push(text);
      text = this.game.add.text(this.center.x+260, this.center.y*1.72, this.scores[2], 
      {fontSize: '80px', fill: "#006400"});
      this.textScores.push(text);
    }
    
    erase() {
      if (this.mainStatus != null) {
        this.mainStatus.destroy();
      }
      if (this.mainSprite != null) {
        this.mainSprite.destroy();
      }
    }

}

export default Status;
